













import {Component, Prop} from 'vue-property-decorator';
import Vue from 'vue';

@Component
export default class NoteSettingDialog extends Vue {
    @Prop(String) note: string | undefined;

    note_ = this.note;

    onOkClicked(){
        this.$emit('update:note',this.note_)
        this.$emit('update:isShow',false)
    }
}
