import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class LayoutWithBackAndTitle extends Vue {
    mounted() {
        vhCheck();
    }

    back() {
        // this.$router.push("home")
        this.$emit('onBackClicked')
        this.$router.go(-1);
    }
}
